/* material checkboxes */
.cb-label {
  display: inline;
  cursor: pointer;
}
.checkbox-material .checkbox,
.checkbox-simple .checkbox {
  display: inline-block;
  line-height: 1;
}
.checkbox-material .checkbox input,
.checkbox-simple .checkbox input {
  opacity: 0;
}
.checkbox-material .checkbox input[disabled] + span,
.checkbox-simple .checkbox input[disabled] + span {
  opacity: 0.5;
}
.checkbox-material .checkbox span {
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding: 0.15em 1.5em 0.15em 0.15em;
  transition: all ease 0.2s;
  -webkit-transition: all ease 0.2s;
  -o-transition: all ease 0.2s;
  -moz-transition: all ease 0.2s;
  border-radius: 1em;
  background-color: grey;
  overflow: hidden;
}
@media (prefers-color-scheme: dark) {
  .checkbox-material .checkbox span {
    background-color: #848991;
  }
}
.checkbox-material .checkbox span::before {
  display: block;
  content: '';
  width: 1em;
  height: 1em;
  border-radius: 1em;
  background: white;
}
.checkbox-material .checkbox span::after {
  position: absolute;
  display: block;
  text-transform: uppercase;
  font-size: 0.5em;
  font-style: normal;
  top: 0;
  color: #fff;
  line-height: 1;
  left: 0;
  padding-left: 2em;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  height: 1em;
  bottom: 0;
  margin: auto;
}
.checkbox-material .checkbox :checked + span::after {
  padding-left: 0;
  padding-right: 2em;
}
.checkbox-material .checkbox :checked + span {
  padding-right: 0.15em;
  padding-left: 1.5em;
  background-color: #F39619;
}
@media (prefers-color-scheme: dark) {
  .checkbox-material .checkbox :checked + span {
    background-color: #00adb5;
  }
}
.checkbox-material .shiny-input-container {
  display: inline-block;
  zoom: 1;
  *display: inline;
  width: 75px;
  margin: 0;
}
.checkbox-material,
.checkbox-simple {
  cursor: pointer;
  align-items: center;
}
.checkbox-simple > label {
  padding-left: 25px;
  position: relative;
}
.checkbox-simple .checkbox {
  position: absolute;
  left: 0;
  top: 0;
  margin-top: 0;
}
.checkbox-simple .checkbox,
.checkbox-simple .label-span {
  vertical-align: middle;
  display: inline-block;
}
.checkbox-simple .checkbox span {
  position: absolute;
  top: 2px;
  left: 0;
  cursor: pointer;
  overflow: hidden;
  border: 1px solid;
  border-color: #ADADAD;
  background-color: #fff;
  border-radius: 3px;
}
@media (prefers-color-scheme: dark) {
  .checkbox-simple .checkbox span {
    border-color: #A9A9A9;
    background-color: #393e46;
  }
}
.checkbox-simple .checkbox span:hover {
  background-color: #ADADAD;
}
.checkbox-simple .checkbox span::before {
  display: block;
  content: '';
  width: 1em;
  height: 1em;
}
.checkbox-simple .checkbox span::after {
  position: absolute;
  display: inline-block;
  top: 1px;
  left: 1px;
  content: "";
  font-family: "Font Awesome 5 Free";
  padding-right: 3px;
  vertical-align: middle;
  font-size: 13px;
  font-weight: 900;
}
.checkbox-simple .checkbox :checked + span::after {
  display: block;
  content: "\f00c";
}
.checkbox-simple .checkbox :checked + span {
  background-color: #F39619;
  color: #333;
}
@media (prefers-color-scheme: dark) {
