[data-tooltip] {
  cursor: pointer;
  display: inline-block;
  line-height: 1;
  position: relative;
}
[data-tooltip]::after {
  border-radius: 0.3rem;
  content: attr(data-tooltip);
  font-size: 85%;
  font-weight: normal;
  padding: 0.25rem 0.5rem;
  position: absolute;
  text-align: center;
  visibility: hidden;
  z-index: 811;
  /*sidebar has 810 */
  background-color: rgba(51, 51, 51, 0.9);
  color: #fff;
  margin-right: 3px;
  right: calc(110% - 0.3rem);
  margin-top: -0.3rem;
  max-width: 30rem;
  width: max-content;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (prefers-color-scheme: dark) {
  [data-tooltip]::after {
    background-color: rgba(60, 141, 188, 0.9);
    color: #eee;
  }
}
@media (max-width: 767px) {
  [data-tooltip].tooltip-mobile::before {
    display: none;
  }
  [data-tooltip].tooltip-mobile:after {
    font-size: 100%;
    line-height: 100%;
    max-width: 20rem;
    position: fixed;
    bottom: auto;
    top: 50%;
    right: 50%;
    transform: translate(50%);
    text-align: left;
    white-space: normal;
  }
}
[data-tooltip].always-mobile::before {
  display: none;
}
[data-tooltip].always-mobile:after {
  font-size: 100%;
  line-height: 100%;
  max-width: 20rem;
  position: fixed;
  bottom: auto;
  top: 50%;
  right: 50%;
  transform: translate(50%);
  text-align: left;
  white-space: normal;
}
[data-tooltip]:hover::after,
[data-tooltip][class=tooltip-mobile]:hover::before {
  visibility: visible;
  opacity: 1;
}
.sidebar-menu .bt-highlight-3,
.sidebar-menu .bt-highlight-2 {
