/* change mode for static/browser-responsive css compilation */
/* global variables */
/* booleans for all packages */
/* do not change */
.pvtUi {
  color: #333;
}
table.pvtTable {
  font-size: 8pt;
  text-align: left;
  border-collapse: collapse;
}
table.pvtTable tbody tr th,
table.pvtTable thead tr th {
  background-color: #e6EEEE;
  border: 1px solid #CDCDCD;
  font-size: 8pt;
  padding: 5px;
}
table.pvtTable .pvtColLabel {
  text-align: center;
}
table.pvtTable .pvtTotalLabel {
  text-align: right;
}
table.pvtTable tbody tr td {
  color: #3D3D3D;
  padding: 5px;
  background-color: #FFF;
  border: 1px solid #CDCDCD;
  vertical-align: top;
  text-align: right;
}
.pvtGrandTotal,
.pvtTotal {
  font-weight: 700;
}
.pvtVals {
  text-align: center;
  white-space: nowrap;
}
.pvtColOrder,
.pvtRowOrder {
  cursor: pointer;
  width: 15px;
  margin-left: 5px;
  display: inline-block;
}
.pvtAggregator {
  margin-bottom: 5px;
}
.pvtAxisContainer,
.pvtVals {
  border: 1px solid gray;
  background: #EEE;
  padding: 5px;
  min-width: 20px;
  min-height: 20px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
.pvtAxisContainer li {
  padding: 8px 6px;
  list-style-type: none;
  cursor: move;
}
.pvtAxisContainer li.pvtPlaceholder {
  -webkit-border-radius: 5px;
  padding: 3px 15px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  border: 1px dashed #aaa;
}
.pvtAxisContainer li span.pvtAttr {
  -webkit-text-size-adjust: 100%;
  background: #F3F3F3;
  border: 1px solid #DEDEDE;
  padding: 2px 5px;
  white-space: nowrap;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.pvtTriangle {
  cursor: pointer;
  color: grey;
}
.pvtHorizList li {
  display: inline;
}
.pvtVertList {
  vertical-align: top;
}
.pvtFilteredAttribute {
  font-style: italic;
}
.pvtFilterBox {
  z-index: 100;
  width: 300px;
  border: 1px solid gray;
  position: absolute;
  text-align: center;
}
@media (prefers-color-scheme: dark) {
  .pvtFilterBox {
    background-color: #848991;
  }
}
.pvtFilterBox h4 {
  margin: 15px;
}
.pvtFilterBox p {
  margin: 10px auto;
}
.pvtFilterBox label {
  font-weight: 400;
}
.pvtFilterBox input[type=checkbox] {
  margin-right: 10px;
  margin-left: 10px;
}
.pvtFilterBox input[type=text] {
  width: 230px;
}
.pvtFilterBox .count {
  font-weight: 400;
  margin-left: 3px;
}
@media (prefers-color-scheme: dark) {
  .pvtFilterBox .count {
    color: #393e46;
  }
}
.pvtCheckContainer {
  text-align: left;
  font-size: 14px;
  white-space: nowrap;
  overflow-y: scroll;
  width: 100%;
  max-height: 250px;
  border-top: 1px solid #d3d3d3;
  border-bottom: 1px solid #d3d3d3;
}
.pvtCheckContainer p {
  margin: 5px;
}
.pvtRendererArea {
  padding: 5px;
}
